import logo from './logo.svg';
import React, {useState, useRef} from 'react'
import './App.css';
import {questions} from './const';
import swal from 'sweetalert';

let isDisabled = true;

function App() {
    
    let result = 0;
    const [sum, setSum] = useState(0);
    const question1 = useRef();
    const question2 = useRef();
    const question3 = useRef();
    const question4 = useRef();

    const questionRefs = [question1, question2, question3, question4];
    

    


    
    const onInputHandker = (evt) => {
        setSum(evt.target.value);
        isDisabled=!isDisabled;
        if(parseFloat(question1.current.value) == 11) {
            isDisabled = false;
        }
        console.log(parseFloat(question1.current.value));
        console.log(isDisabled);
    }

    const onSubmitHandler = (evt) => {

        evt.preventDefault();
        let defaultSum = 0;
        
        questionRefs.forEach((ref) => {
            if(!isNaN(ref.current.value)) {
              defaultSum = defaultSum + parseFloat(ref.current.value);
             }
        })

        if(defaultSum !== 11) {
            swal('Cума балов не равна 11. Необходимо сделать исправления');
        }
    }
    return (
        <>
        <header className="page-header">
            <div>
                <img src="./img/logo.png" alt="Логотип компании"/>
            </div>
            <p className="page-header__history">Чверть століття тому було покладено початок великого шляху. Сьогодні ми
                також сміливі, як і на самому початку.</p>
        </header>
        <main class="main-container">
            <p class="main-container__title">Первое суждение</p>
            <p class="main-container__qustion">Я полагаю, что мог внести большой вклад на такой работе где</p>
            <form class="qustion-form">
                {questions.map(({questionName, text}, index) => {return (
                   <div class="qustion-form__block">
                   <label class="qustion-form__label" for={questionName}>{`Ответ №${questionName}`}</label>
                   <input ref={questionRefs[index]} placeholder='0' onChange={onInputHandker} class="qustion-form__input" type="number" id={questionName}/>
                       <p class="qustion-form__description">{text}</p>
               </div>
                )} )}                
                <button onClick={onSubmitHandler} type="submit" class="qustion-form__button">Наступне питання</button>
            </form>
        </main>
            </>);
}

export default App;
